.continer-body{
  padding-top: 20px;
  height: 625px;
  /* background-color: ; */
}
.css-selector {
  background: linear-gradient(271deg, #f7f2db, #ffee62, #f59c54);
  background-size: 600% 600%;

  -webkit-animation: AnimationName 14s ease infinite;
  -moz-animation: AnimationName 14s ease infinite;
  animation: AnimationName 14s ease infinite;
}

@-webkit-keyframes AnimationName {
  0%{background-position:0% 50%}
  50%{background-position:100% 51%}
  100%{background-position:0% 50%}
}
@-moz-keyframes AnimationName {
  0%{background-position:0% 50%}
  50%{background-position:100% 51%}
  100%{background-position:0% 50%}
}
@keyframes AnimationName {
  0%{background-position:0% 50%}
  50%{background-position:100% 51%}
  100%{background-position:0% 50%}
}
.continer-body-img{
  display: flex;
  align-items: center;
  justify-content: center;
}
.continer-body-img img{
  width: auto;
  height: auto;
}
.continer-body-info{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.location-position{
  position: absolute;
  top: 0px;
  right: 55px;
  width: 180px;
  border: 2px solid;
  border-radius: 10px;
  text-align: center;
}
.continer-body-info-form{
  width: 90%;
  min-height: 200px;
  background-color: whitesmoke;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  /* height: ; */
}
.continer-body-info-form-select{
  
}

.select-product-type{
    outline: none;
    border-bottom: 1px solid;
    width: 100%;
    margin-top: 15px;
    border: 0 0 1 0;
    border: 0;
    font-size: 20px;
    border-radius: 0;
    /* padding: 10px; */
    border-bottom: thin solid #e1922b;
    /* border-radius: 4px; */
    display: inline-block;
    font: inherit;
    line-height: 1.5em;
    padding: 0.5em 3.5em 0.5em 1em;
    margin: 0; 
    background: whitesmoke;     
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;
}

.select-product-type option{
  background-color: azure;
  padding-top: 10px;
}
.input-product-type{
